#services{
    margin-top: -80px;
    padding-top: 0;
}
.service___container{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Responsive */
    gap: 2rem;
    justify-content: center;
    align-items: start;
   
  
}
.service{
    background: var( --color--bg--variant);
    border-radius: 0 0 2rem 2rem;
    border: 1px solid var(--color--primary);
    height: fit-content;
    transition: var(--transition);
}
.service:hover{
    background: transparent;
    border-color: var( --color--primary--variant);
    cursor: default;
}
.service__head{
    background: var(--color--primary);
    padding: 2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem rgba(0, 0,0);
}
.service__head h3{
    color: var( --color--bg) ;
    font-size: 1rem;
    text-align: center;
}
.service__list{
    padding: 2rem;
}
.service__list li{
    display: flex;
    gap: 1rem;
    margin-bottom: 0.8rem;
}
.service__list-icon{
    color:var(--color--primary);
   margin-top: 2px;
}
.service__list p{
font-size: 0.9rem;
}
/* ***************media queries*********** */
@media screen and (max-width: 1024px){
    .services__container{
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
    }
    .service{
        height: auto;
    }
}
/* *********big screen************* */
@media screen and (max-width: 600px){
    .service___container{
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
}