header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
 display: block;
}
.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}
/* }*********************CTA***************** */
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* ******************header social************* */
.header_socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-area: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}
.header_socials::after{
    content: "";
    width: 1px;
    height: 2px;
    background: var(--color--primary);
    
}
/* ,,,,,,,,,,,,,,,,,Me............... */
.me{
    background: linear-gradient(var(--color--primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;

}
/* ************scroll Down*********** */
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}
/* *****************media quries */
@media screen and (max-width: 1024px){
header{
    height: 68vh;
}
}
@media screen and(max-width: 600px) {
    header{
        height: 100vh;

    }
    .header_socials, .scroll_down{
        display: none;
    }
}