.testimonia__container{
    width: 40%;
}
.client__avatar{
    width: 4rem;
    aspect-ratio: 1/1;
    overflow: hidden;
    border-radius: 50%;
    margin: 0 auto 1rem;
    border: 0.4rem solid var(--color--primary--variant);
}
#testimonia>h5{
    margin-top: 400px;
}
#testimonia>h2{
    margin-top: 8px;
}
.testimonia{
    background: var( --color--bg--variant);
    text-align: center;
    padding: 2rem;
    border-radius: 2rem;
    user-select: none;
}
.client__review {
  color:var( --color--light);
  font-weight: 300;
  display: block;
  width: 80%;
  margin: 0.8rem auto 0;
}
/* *========media queries (medium devices)**================= */
@media screen and (max-width:1024px){
    .testimonia__container{
        width: 60%;
    }
}
/* =============media queries (small device)**************** */

@media screen and (max-width:600px){
    .testimonia__container{
        width: var(--container--width-sm);
    }
    .client__review{
        width: var(--container--width-sm);
    }
    }